import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

import Title from "../components/layout/title"

export default function PageCategoryTemplate({ pageContext: pageCategory }) {
  return (
    <Layout>
      <SEO title={`Category  - ${pageCategory.Name}`} />

      <div>
        <Title>{pageCategory.Name}</Title>

        <ul>
          {pageCategory.pages.map((page, key) => (
            <li key={key}>
              <Link to={`/${pageCategory.UID}/${page.UID}`}>{page.Title}</Link>
            </li>
          ))}
        </ul>
      </div>
    </Layout>
  )
}
